ious to generate manually : I start from path data from Inkscape or Illustrator, import it into Blender, extrude it, offset it with Offset Scale (Alt + S), intersect it with a regular cube to suppress distorsion from Offset Scale, export it to a STL file, clean it in Netfabb, and then slice it in Cura.")]),_c('figure',[_c('img',{attrs:{"src":"pictures/letter_h_svg.jpg"}}),_c('span',{staticClass:"author"},[_vm._v("\"h\" bold italic in Input from FontBureau, in Inkscape")]),_c('img',{attrs:{"src":"pictures/letter_h_blender.jpg"}}),_c('span',{staticClass:"author"},[_vm._v("after an import and offset extrusion in Blender")]),_c('img',{attrs:{"src":"pictures/letter_h_netfabb.jpg"}}),_c('span',{staticClass:"author"},[_vm._v("then imported into Netfabb")]),_c('img',{attrs:{"src":"pictures/letter_h_cura.jpg"}}),_c('span',{staticClass:"author"},[_vm._v("and finally sliced in Cura")])]),_c('p',[_vm._v("This process is tedious. I thought I could automate it, starting from SVG path data. The rest of this page shows how this was way more complex than I thought, and, after a week of exploration, it's obvious I should have thought of intersecting paths, paths with holes, concave shapes, and a lot more traps.")]),_c('figure',[_c('img',{attrs:{"src":"pictures/u_3d.jpg"}}),_c('img',{attrs:{"src":"pictures/u_3d_print.jpg"}})])])}]

